import { Component, AfterViewInit, OnInit } from '@angular/core';
import * as sha1 from 'sha1';

declare var $: any;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements AfterViewInit, OnInit {

  private password = '601f1889667efaebb33b8c12572835da3f027f78';
  private tryMe: string;
  private locked: boolean;
  private isMobile = true;

  private config: SwiperOptions = {
    pagination: '.swiper-pagination',
    paginationClickable: true,
    nextButton: '.swiper-button-next',
    prevButton: '.swiper-button-prev',
    spaceBetween: 30
  };

  ngOnInit(): void {
    this.locked = true;
    this.tryMe = '';
  }

  ngAfterViewInit(): void {
    const screenW: number = $(window).width();
    if (screenW < 600) {
      const screenH = $(window).height();
      $('#video-source').css('height', screenH + 'px');
    } else {
      this.isMobile = false;
    }
  }

  private doIt(key: string): void {
    this.tryMe += key;

    if (this.tryMe.length === 6) {
      const yourChance: string = sha1(this.tryMe);
      if (yourChance === this.password) {
        this.locked = false;
      } else {
        $('.teste').addClass('error');
        setTimeout(() => { $('.teste').removeClass('error'); }, 300);
      }
    }
  }
}



